/* https://github.com/facebookincubator/create-react-app/issues/1283 */
:root {
  --neutral-lightest: #fff;
  --neutral-lighter: #f9f9fb;
  --neutral-light: #e6e6e6;
  --neutral-medium: #aaadb3;
  --neutral-neutral: #5b5d5d;
  --neutral-base: #333;
  --neutral-darker: #1f2532;
  --neutral-darkest: #000;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}
body {
  margin: 0;
  padding: 0;
  color: #333;
  color: var(--neutral-base);
  background-color: #e6e6e6;
  background-color: var(--neutral-light);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

*, *::before, *::after {
  box-sizing: border-box;
}

a,
a:visited {
  color: #1f2532;
  color: var(--neutral-darker);
}

a:hover {
  color: #5b5d5d;
  color: var(--neutral-neutral);
}
.Paragraph{
    font-size: 1.8rem;
}
.Title {
    text-align: center;
    font-size: 3rem;
    text-transform: uppercase;
    letter-spacing: 9px;
    font-weight: 300;
    color: gray;
}
.About__expertises{
    display: flex;
    width: 100%;
    flex-wrap: wrap;
}
.Expertise{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.Expertise__icon{
    font-size: 3rem;
}

.Expertise__description{
    padding: 0 2rem;    
}

.App {
  background-color: var(--neutral-light);
}

.Page {
  flex: 1 1;
  background-color: var(--neutral-light);
}

.Page__inner {
  margin: 16px 0;
  position: relative;
  top: -93px;
}

@media only screen and (min-width: 620px) {
  .Page__inner {
    position: relative;
    top: -85px;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 0 32px;
    margin-bottom: -32px;
  }
}

@media only screen and (min-width: 1000px) {
  .Page__inner {
    top: -90px;
    margin-bottom: -98px;
  }
}

.Page__section {
  max-width: 900px;
  background: var(--neutral-lightest);
  border: 1px solid var(--neutral-light);
  box-shadow: 0 1px 0 0 #e5e5e5;
  padding: 16px 16px 32px;

  margin: 0 auto 16px;
}

@media only screen and (min-width: 400px) {
  .Page__section {
    padding: 16px 32px 32px;
  }
}

@media only screen and (min-width: 620px) {
  .Page__section {
    border-radius: 6px;
    margin: 0 auto 32px;
  }
}

.Page__section:last-child {
  margin-bottom: 0px;
}
.Contacts{
    text-align: center;
    margin-bottom: 2rem;
}

.Contacts__icons{
    font-size: 2rem;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    background-color: grey;
    cursor: pointer;
    margin: 1rem 1rem 0 1rem;
    padding: 1rem 1rem 0 1rem;
    line-height: 1;
    color: white;

}
.Header {
    background: gray;
  }
  
  .Header__banner {
    background-color: #666;
    background-image: url("/assets/img/banner.jpg");
    background-position: top left, center center;
    background-repeat: repeat, no-repeat;
    background-size: auto, cover;
    color: #fff;
    padding: 8em 0 6em;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .Header__logo {
    border-radius: 50%;
    height: 13rem;
    -webkit-filter: none;
            filter: none;
  }
  
  .Header__name {
    font-size: 3.4rem;
    font-weight: normal;
  }
  
  .Header__job {
    margin-top: 0;
    font-size: 2rem;
  }
  
  @media only screen and (min-width: 500px) {
    .Header__banner {
      padding: 6em 0 8em;
    }
  }
  
  @media only screen and (min-width: 650px) {
    .Header__banner {
      padding: 6em 0 8em;
    }
  }
  
  @media only screen and (min-width: 1000px) {
    .Header__banner {
      padding: 6em 0 8em;
    }
  }
.ProgressBar{
    width: 100%;
    height: 0.4rem;
    background-color: rgba(107, 112, 118, 0.4)
}

.ProgressBar__bar{
    background-color: grey;
    height: 0.4rem;
}
.Skills {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    height: 13rem;
}

.Skills__item {
    width: 40%;
}
.Experience {
    display: flex;
    height: 8rem;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.3); 
    margin-bottom: 2rem;
}
.Experience__left {
    padding: 1rem;
    color: white;
    background-color: rgba(107, 112, 118, 0.4);
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;
}
.Experience__range{
    font-size: 1.3rem;
}
.Experience__company{
    font-size: 1.8rem;
}
.Experience__right {
    flex:1 1;
    padding: 1rem;
}

.Experience__title{
    font-size: 1.8rem;
    text-transform: uppercase;
}

